import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {IUserEnabling} from './model/IUserEnabling';


@Component({
  selector: 'shared-user-enabling',
  templateUrl: './user-enabling.component.html',
  styleUrls: ['./user-enabling.component.css']
})
export class UserEnablingComponent implements OnInit {

  @Output('enablingManagerSave') enablingManagerSaveEmitter = new EventEmitter<Event>();
  @Output('enablingManagerCancel') enablingManagerCancelEmitter = new EventEmitter<Event>();
  @Output('enablingManagerSelect') enablingManagerSelectEmitter = new EventEmitter<Event>();
  @Input() userEnablingList: IUserEnabling[];
  @Input() selectedLabel: string = 'crudForm.enablingManager.table.default'
  @Input() showSave: boolean = true;
  @Input() showCancel: boolean = true;

  userEnablingSelected: IUserEnabling = null;
  constructor() { }

  ngOnInit() {
  }

  public enablingManagerSave(event){
    this.enablingManagerSaveEmitter.emit(event);
  }

  public enablingManagerCancel(event){
    this.enablingManagerCancelEmitter.emit(event);
  }

  public enablingManagerSelect(event){
    this.enablingManagerSelectEmitter.emit(event);
  }

  public checkedChange(userEnablingChecked: IUserEnabling){
    this.userEnablingSelected = userEnablingChecked;
    this.userEnablingList.forEach((userEnabling)=>{
      if(userEnabling.groupName!== '' && userEnabling.groupName !== userEnablingChecked.groupName){
        userEnabling.enabling.defaultEnabling = false;
      }else if(userEnabling.roleName!== '' && userEnabling.roleName !== userEnablingChecked.roleName){
        userEnabling.enabling.defaultEnabling = false;
      }
    });

    this.enablingManagerSelect(this.userEnablingSelected);

  }

}
