import { Action, createReducer, on } from '@ngrx/store';
import { IUserInfo } from '../access-checker/model/userInfo.interface';
import *  as sharedActions from './shared.actions';

export interface ISharedState {
  workspaceId :  string
  pageId: string 
  languageCode : string,
  countryCode : string,
  timezoneCode  :string

};
 
const initialSharedState : ISharedState = {workspaceId : null , pageId:  null , countryCode : null , languageCode : null , timezoneCode  : null }
 
const eventReducer = createReducer(
  initialSharedState,
  on(sharedActions.setWorkSpace, (state , {workspaceID}) => ({ ...state, workspaceId : workspaceID })),
  on(sharedActions.setPage, (state , {pageID}) => ({ ...state, pageId : pageID })),
  on(sharedActions.setLocaleInfo, (state , {languageCode , countryCode , timezoneCode}) => ({ ...state, languageCode : languageCode,countryCode : countryCode, timezoneCode  :timezoneCode})),
  
  
  );



export function reducer(state: ISharedState | undefined, action: Action) {
  return eventReducer(state, action);
}

