import { Component, OnInit } from '@angular/core';
import {NbDialogRef} from '@nebular/theme';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'shared-dialog-message',
  templateUrl: './dialog-message.component.html',
  styleUrls: ['./dialog-message.component.scss']
})
export class DialogMessageComponent implements OnInit {

  public title: string;
  public message: string;
  //public titleTranslated: string;
  //public messageTranslated: string;
  public isConfirmationMode: boolean;

  constructor(protected ref: NbDialogRef<DialogMessageComponent>, public translate: TranslateService) { }

  ngOnInit() {
    //this.titleTranslated = this.translate.instant(this.title);
    //this.messageTranslated = this.translate.instant(this.message);
  }

  cancel() {
    this.ref.close(false);
  }

  confirm() {
    this.ref.close(true);
  }


}
