
import { createAction, props } from '@ngrx/store';
import { ISharedState } from './shared.reducer';


export const setWorkSpace = createAction(
  '[shared] set workspace',
  props<{ workspaceID: string }>()
);

export const setPage = createAction(
  '[shared] set page',
  props<{ pageID: string }>()
);

export const setService = createAction(
  '[shared] set service',
  props<{ serviceID: string }>()
);

export const setPermissionsAndUserInfo = createAction(
  '[shared] set permissions And User Info',
  props<{ userInfo: ISharedState }>()
);

export const setLocaleInfo = createAction(
  '[shared] set locale info',
  props<{
    languageCode: string,
    countryCode: string,
    timezoneCode: string
  }>()
);









