import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';

@Component({
  selector: 'shared-close-form-action',
  templateUrl: './close-form-action.component.html',
  styleUrls: ['./close-form-action.component.css']
})
export class CloseFormActionComponent implements OnInit {

  @Output('closeEditForm') closeEditFormEmitter = new EventEmitter<Event>();
  @Input() tooltip: string;

  constructor() { }

  ngOnInit() {
  }

  public close(event){
    this.closeEditFormEmitter.emit(event);
  }

}
