import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { NbAuthService } from '@nebular/auth';
import { Observable } from 'rxjs';
import { tap } from 'rxjs/operators';
import { AccessCheckerService } from '../access-checker/access-checker.service';




@Injectable({
  providedIn: 'root'
})
export class AuthGuardService implements CanActivate {

  readonly whiteList = ['/', 'unauthorized'];

  constructor(private authService: NbAuthService, private router: Router, private acs: AccessCheckerService) { }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {

    return this.authService.isAuthenticated()
      .pipe(
        tap(authenticated => {

          if (!authenticated) {
            this.router.navigate(['auth/login']);
          }
          else{

            



           this.checkPermission(route,state);
        /*   if (Object.keys(this.acs.getPemissions()).length === 0) {
            this.acs.setPermissionsAndUserInfo(this.acs.getACL())

          }  */

        }
        }),
      );
  }

  private checkPermission(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
    if (route.url.length > 0 && !this.whiteList.includes(route.url[route.url.length - 1].path)) {

      let featureOrPageId = this.getFeatureOrPageId(state);
     
      if (this.isComposerPage(state) ) {
        if (!this.acs.isPageRouteGranted(featureOrPageId))
        this.router.navigate(['auth/unauthorized']);
      } else if (!this.acs.isFeatureRouteGranted(featureOrPageId)) {
        this.router.navigate(['auth/unauthorized']);
      }
    }
  }

  private isComposerPage(state: RouterStateSnapshot) {
    if (state.url.indexOf('view') > -1) {
      return true;
    } else {
      return false
    }
  }

  private getFeatureOrPageId(state: RouterStateSnapshot) {

    let splitArray = state.url.split('/');
    return splitArray[splitArray.length - 1];
  }

  private checkUrlContainsString(filter: string) {

    
  }

}
