import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Inject } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import { IUserEnabling } from '../model/IUserEnabling';

@Injectable({
    providedIn: 'root'
})

export class UserEnableApiService {

    private COMMON_URL = `${this.environment.apiTarget}/rs/public/`;
  constructor(private httpClient: HttpClient ,  @Inject('env') private environment) { }



  getEnabling(ownerUuid: string):Observable<IUserEnabling[]> {
    const headers = new HttpHeaders().set('Content-Type', 'application/json');
     return this.httpClient.post<IUserEnabling[]>(`${this.COMMON_URL}getAbilitationsWSR`, JSON.stringify({keyUuid: ownerUuid}), {headers: headers}).pipe(
       catchError(error => {
        return throwError('getEnablings Something went wrong! ' + error);
      })
    );
  }



}