import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';

@Component({
  selector: 'shared-default-form-actions',
  templateUrl: './default-form-actions.component.html',
  styleUrls: ['./default-form-actions.component.css']
})
export class DefaultFormActionsComponent implements OnInit {

  @Input() isEditMode: boolean;
  @Input() form: any;

  //REMOVE
  @Output('remove') removeEmitter = new EventEmitter<Event>();
  @Input() tooltipRemove: string;

  //enableEditForm
  @Output('enableEditForm') enableEditFormEmitter = new EventEmitter<Event>();
  @Input() tooltipEnableEditForm: string;

  //UPDATE
  @Output('update') updateEmitter = new EventEmitter<Event>();
  @Input() tooltipUpdate: string;

  //SAVE
  @Input() tooltipSave: string;

  public remove(event){
    this.removeEmitter.emit(event);
  }

  public enableEditForm(event){
    this.enableEditFormEmitter.emit(event);
  }

  public update(event){
    this.updateEmitter.emit(event);
  }



  constructor() { }

  ngOnInit() {
  }

}
