import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';

@Component({
  selector: 'shared-header-crud',
  templateUrl: './header-crud.component.html',
  styleUrls: ['./header-crud.component.css']
})
export class HeaderCrudComponent implements OnInit {

  @Output('helpPage') helpPageEmitter = new EventEmitter<Event>();
  @Output('add') addEmitter = new EventEmitter<Event>();
  @Input() tooltipText: string;
  @Input() title: string;
  @Input() addButtonText: string;

  constructor() { }

  ngOnInit() {
  }

  public helpPage(event){
    this.helpPageEmitter.emit(event);
  }

  public add(event){
    this.addEmitter.emit(event);
  }

}
