import { Injectable,  } from '@angular/core';
import { Actions, createEffect, ofType, } from '@ngrx/effects';
import { TranslateService } from '@ngx-translate/core';
import { tap } from 'rxjs/operators';
import { LocaleService } from '../locale/locale.service';
import { setLocaleInfo } from './shared.actions';





@Injectable()
export class SharedEffect {

  constructor(private actions$: Actions , private translate: TranslateService, private localeService : LocaleService  ) {

  }

   setLocalInfo$ = createEffect(() => this.actions$.pipe(
    ofType(setLocaleInfo),
    tap( action => {
      this.translate.use(action.languageCode);    
      this.localeService.registerCulture(action.languageCode);
    }),
  ),
  { dispatch: false }); 


}
