import { Injectable } from '@angular/core';
import { Actions, ofType, createEffect } from '@ngrx/effects';
import { first, tap } from 'rxjs/operators';
import { defaultSuccessMessage, failureMessage, warningMessage } from './toastr.actions';
import { NbToastrService } from '@nebular/theme';

import { TranslateService } from '@ngx-translate/core';





@Injectable(
  {
    providedIn: 'root'
  }
)
export class ToastrEffects {


  defaultSuccessMessage$ = createEffect(() =>
    this.actions$.pipe(
      ofType(defaultSuccessMessage),
      tap(action => {
        let title;
        this.translate.get('toastr.operationSuccess').pipe(first()).subscribe(text =>  this.toastrService.success('', text) );
       
      }),

    ),
    { dispatch: false }
  );

  errorMessage$ = createEffect(() =>
    this.actions$.pipe(
      ofType(failureMessage),
      tap(action => { 
        this.translate.get([action.message, action.title]).pipe(first()).subscribe(text => {this.toastrService.danger(text[Object.keys(text)[0]], text[Object.keys(text)[1]]) } );
      }),

    ),
    { dispatch: false }
  );


  warningMessage$ = createEffect(() =>
    this.actions$.pipe(
      ofType(warningMessage),
      tap(action => { this.toastrService.warning('', action.message, { destroyByClick: true, duration: 0 }) }),

    ),
    { dispatch: false }
  );

  constructor(
    private actions$: Actions,
    private toastrService: NbToastrService,
    private translate: TranslateService
  ) { }
}
