import { createFeatureSelector, createSelector } from '@ngrx/store';
import { ISharedState } from './shared.reducer';

export const featureShared = 'shared';

export interface AppSharedState {
  shared: ISharedState;
}

export const selectFeature = createFeatureSelector<AppSharedState, ISharedState>(featureShared);
 


export const selectWorkspace = createSelector(
  selectFeature,
  (state: ISharedState) => state.workspaceId
);

export const selectPage = createSelector(
  selectFeature,
  (state: ISharedState) => state.pageId
);

