import { Component, OnInit } from '@angular/core';
import { NbDialogRef } from '@nebular/theme';

@Component({
  selector: 'shared-confirmationMessageDialog',
  templateUrl: './confirmationMessageDialog.component.html',
  styleUrls: ['./confirmationMessageDialog.component.scss']
})
export class ConfirmationMessageDialogComponent implements OnInit {

  constructor(protected ref: NbDialogRef<ConfirmationMessageDialogComponent>) {}

  ngOnInit() {

  }

  cancel() {
    this.ref.close(false);
  }

  confirm() {
  
    this.ref.close(true);
  }
}
